import React, {useEffect, useState} from "react"

const Chat = ({socket}) => {
	const [message, setMessage] = useState("")
	const [list, setList] = useState([])

		useEffect(() => {
				socket.on('mes', (data) => {
					setList(list => list.concat(data))
				})
		},[socket])

	const handleSend = (e) => {
		const code = e.keyCode
		if (code === 	13){
			if (message) {
				socket.emit("mes", message)
				setMessage("")
			}
		}
	}

	if (list.length > 10) {
		setList(list.slice(list.length - 10, list.length))
	}
	
	return(
		<div id="chat">
			<ul>
      {list.map((x, i) => 
      <li key={i}>
				<div> {x.from}</div>
        <div>{x.message}</div>
      </li>)}
			</ul>
			<input
				type="text"
				placeholder="quess or message"
				value={message}
				onChange={({ target }) => setMessage(target.value)}
				onKeyDown={handleSend}
			/>
		</div>
	)
}

export default Chat